*,html 
{
  background-color:darkred;
  color:white;
  padding:0;
  margin:0;
}


.box
{
  margin-left:auto;
  margin-right: auto;
  max-width: 700px;
}

input[type="text"]
{
  outline: none;
}

input[type="text"]:focus
{
  outline: 0px;
  box-shadow: none;
}



@media screen and (max-width:600px) 
{
.col-sm-3 
{
text-align: center;
}
}
